[data-annotation-mode=true] [data-annotation-type] {
  display: inline-block;
  background-color: #FFF;
  border: 1px solid lightblue;
  border-radius: 10px;
  padding: 5px 10px !important;
  margin-top: 5px;
  cursor: pointer;
}
[data-annotation-mode=true] [data-annotation-type][data-annotation-selected=true] {
  background-color: aliceblue;
}